.header {
    background: #0452a5;
    background: -moz-linear-gradient(-45deg, #0452a5 0%, #0053a7 50%, #2d60d8 79%, #4779f3 100%);
    background: -webkit-linear-gradient(-45deg, #0452a5 0%, #0053a7 50%, #2d60d8 79%, #4779f3 100%);
    background: -webkit-linear-gradient(315deg, #0452a5 0%, #0053a7 50%, #2d60d8 79%, #4779f3 100%);
    background: -o-linear-gradient(315deg, #0452a5 0%, #0053a7 50%, #2d60d8 79%, #4779f3 100%);
    background: linear-gradient(135deg, #0452a5 0%, #0053a7 50%, #2d60d8 79%, #4779f3 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#0452a5', endColorstr='#4779f3', GradientType=1);
    display: inline-block;
    width: 100%
}

.header .logo {
    display: block;
    color: #fff;
    font-size: 1.25rem;
    font-weight: 700;
    padding: 20px 0
}

.subnav {
    background: #253246;
    padding-top: 15px;
    padding-bottom: 15px;
    top: 0;
    position: -webkit-sticky;
    position: sticky;
    -webkit-box-shadow: inset 0 2px 5px 0 rgba(0, 0, 0, .2);
    -moz-box-shadow: inset 0 2px 5px 0 rgba(0, 0, 0, .2);
    box-shadow: inset 0 2px 5px 0 rgba(0, 0, 0, .2)
}

.subnav ul {
    margin: 0;
    padding: 0;
    list-style: none;
    overflow: hidden
}

.subnav li .menu-btn:hover,
.subnav li:hover {
    background-color: #334055
}

.subnav li label {
    display: block;
    font-size: 1rem;
    color: #a8adb6;
    cursor: pointer;
    text-decoration: none;
    padding: 15px
}

.subnav .menu {
    clear: both;
    max-height: 0;
    -webkit-transition: max-height .3s ease-out;
    -o-transition: max-height .3s ease-out;
    transition: max-height .3s ease-out
}

.subnav .menu-icon {
    cursor: pointer;
    display: inline-block;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    top: -5px
}

.subnav .menu-icon .navicon {
    background: #fff;
    display: inline-block;
    height: 2px;
    position: relative;
    -webkit-transition: background .2s ease-out;
    -o-transition: background .2s ease-out;
    transition: background .2s ease-out;
    width: 18px
}

.subnav .menu-icon .navicon:after,
.subnav .menu-icon .navicon:before {
    background: #fff;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    -webkit-transition: all .2s ease-out;
    -o-transition: all .2s ease-out;
    transition: all .2s ease-out;
    width: 100%
}

.subnav .menu-icon .navicon:before {
    top: 5px
}

.subnav .menu-icon .navicon:after {
    top: -5px
}

.subnav .menu-btn {
    display: none
}

.subnav .menu-btn:checked~.menu {
    max-height: inherit
}

.subnav .menu-btn:checked~.menu-icon .navicon {
    background: 0 0
}

.subnav .menu-btn:checked~.menu-icon .navicon:before {
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    transform: rotate(-45deg);
    opacity: .7
}

.subnav .menu-btn:checked~.menu-icon .navicon:after {
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    opacity: .7
}

.subnav .menu-btn:checked~.menu-icon .label-name {
    opacity: .7
}

.subnav .menu-btn:checked~.menu-icon:not(.steps) .navicon:after,
.subnav .menu-btn:checked~.menu-icon:not(.steps) .navicon:before {
    top: 0
}

.subnav .label-name {
    position: absolute;
    top: 8px;
    left: 25px;
    display: inline-block;
    color: #fff;
    font-size: .85rem;
    text-transform: uppercase;
    font-weight: 700
}

.subnav .by-termsfeed {
    position: absolute;
    font-size: 16px;
    top: 1px;
    right: 20px
}

.subnav .by-termsfeed a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    color: #d3d6da;
    text-decoration: none;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s
}

.subnav .by-termsfeed a:hover {
    color: #fff
}

.subnav .by-termsfeed a:hover img {
    opacity: 1
}

.subnav .by-termsfeed a img {
    max-height: 18px;
    opacity: .7;
    margin-right: 7px;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s
}

.container {
    position: relative;
    max-width: 980px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 20px;
    padding-right: 20px
}

.page {
    margin: 0;
    padding: 0
}

.page input,
.page section>div {
    opacity: 0;
    display: none;
    -webkit-animation: fade 1s;
    -moz-animation: fade 1s;
    -o-animation: fade 1s;
    animation: fade 1s
}

.page #de:checked~section .de,
.page #en:checked~section .en,
.page #es:checked~section .es,
.page #fr:checked~section .fr,
.page #it:checked~section .it,
.page #nl:checked~section .nl {
    display: block;
    opacity: 1
}

.page #de:checked~.subnav .container .de label,
.page #en:checked~.subnav .container .en label,
.page #es:checked~.subnav .container .es label,
.page #fr:checked~.subnav .container .fr label,
.page #it:checked~.subnav .container .it label,
.page #nl:checked~.subnav .container .nl label {
    color: #fff;
    background: #334055
}

@-webkit-keyframes fade {
    0% {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@-moz-keyframes fade {
    0% {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@-o-keyframes fade {
    0% {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

@keyframes fade {
    0% {
        opacity: 0
    }
    to {
        opacity: 1
    }
}

.page footer {
    font-size: .875rem;
    color: #d7d7d9;
