    font-size: 1rem;
    list-style-type: disc;
    line-height: 2;
    margin-left: 30px;
    margin-top: 15px;
    margin-bottom: 15px
}

section a {
    color: #444;
    text-decoration: none;
    -webkit-transition: color .3s;
    -o-transition: color .3s;
    transition: color .3s
}

section a:hover {
    color: #000
}

section pre {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    overflow: auto;
    overflow-y: hidden;
    font-size: 14px;
    line-height: 20px;
    background: #fff6d0;
    border: 1px solid #e6dcae;
    white-space: pre-line;
    white-space: -moz-pre-line;
    white-space: -pre-line;
    white-space: -o-pre-line;
    word-wrap: break-word;
    border-radius: 4px;
    margin: 20px 0;
    padding: 15px
}

@media(max-width:479px) {
    .menu li:first-of-type {
        margin-top: 20px
    }
}

@media(min-width:480px) and (max-width:767px) {
    .menu li:first-of-type {
        margin-top: 20px
    }
}

@media(min-width:768px) and (max-width:979px) {
    .menu li:first-of-type {
        margin-top: 20px
    }
}

@media(min-width:980px) {
    .header .logo {
        font-size: 1.75rem;
        padding: 20px 0
    }
    .subnav {
        background: #253246;
        padding-top: 0;
        padding-bottom: 0
    }
    .subnav li {
        float: left
    }
    .subnav li:hover {
        border-radius: 0
    }
    .subnav li label {
        font-size: 1rem;
        padding: 15px 15px 15px 20px
    }
    .subnav .label-name {
        display: none
    }
    .subnav .menu {
        clear: none;
        max-height: none
    }
    .subnav .menu li:last-of-type {
        margin-bottom: 0
    }
    .subnav .menu-icon {
        display: none
    }
    .subnav .by-termsfeed {
        top: 15px
    }
    section {
        padding: 10px 20px 40px
    }
    section h1 {
        font-size: 2.125rem
    }
    section h2 {
        font-size: 1.65rem
    }
    section h3 {
        font-size: 1.35rem
    }
    section h4 {
        font-size: 1.25rem
    }
    section h5 {
        font-size: 1.125rem
    }
    section h6 {
        font-size: 1rem
    }
    section ul li p {
        margin-top: 0;
        margin-bottom: 10px
    }
    footer {
        font-size: 1rem!important
    }
}